import React, {useState,useEffect} from 'react';

const Allcustomer=()=>{
    const[list,updatelist]=useState([]);
    
    const getlist=()=>{
        let url ="http://firstenquiry.com/api/react/customerlist.php"
        fetch(url)
        .then(response=>response.json())
        .then(result=>updatelist(result))

    }
    useEffect(()=>{
        getlist();
       
    
    })
    


    return(
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <h2 className="text-primary">Customer List</h2>
                    <table className="table table-bordered table-sm">
                        <thead>
                            <tr className="bg-secondary text-white">
                                
                                <td>Name</td>
                                <td>Mobile</td>
                                <td>E-Mail</td>
                                <td>City</td>
                                <td>Address</td>
                                <td>Service</td>
                                <td>Feedback</td>
                                <td>Remarks</td>
                                <td>Next Call</td>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                list.map((xlist,index)=>{
                                    return(
                                    <tr key={index}>
                                        <td>{xlist.name}</td>
                                        <td>{xlist.mobile}</td>
                                        <td>{xlist.email}</td>
                                        <td>{xlist.city}</td>
                                        <td>{xlist.address}</td>
                                        <td>{xlist.service}</td>
                                        <td>{xlist.feedback}</td>
                                        <td>{xlist.remarks}</td>
                                        <td>{xlist.nextcall}</td>

                                    </tr>
                                    )
                                })

                            }
                            </tbody>
                    </table>

                </div>
            </div>
        </div>
    )


}
export default Allcustomer;